import React, { useRef, useState } from "react";
import { useSelector } from "react-redux";
import classes from "./Intro.module.css";
import { BsFillPlayFill, BsPauseFill } from "react-icons/bs";
import meal from "../../assets/meal.mp4";
import altVideo from "../../assets/altVideo.mp4";

const Intro = () => {
  const isAuth = useSelector((state) => state.auth.isAuth);
  const [playVideo, setPlayVideo] = useState(false);
  const videoRef = useRef();

  const playVideoHandler = () => {
    setPlayVideo((prevState) => !prevState);

    if (playVideo) {
      videoRef.current.pause();
    } else {
      videoRef.current.play();
    }
  };

  return (
    <section className={classes.video}>
      <video
        src={isAuth ? altVideo : meal}
        ref={videoRef}
        controls={false}
        type="video/mp4"
        loop
        muted
      />
      <div className={`${classes[`video-overlay`]} flex-center`}>
        <div
          onClick={playVideoHandler}
          className={`${classes[`video-overlay-circle`]} flex-center`}
        >
          {playVideo && <BsPauseFill size={30} color="#fff" />}
          {!playVideo && <BsFillPlayFill size={30} color="#fff" />}
        </div>
      </div>
    </section>
  );
};

export default Intro;
